import service from '../../service.js';
// import uniIcons from "@/components/uni-icons.vue";
import headers from '@/components/header_nav.vue';
export default {
	components: {
		// uniIcons,
		headers
	},
	data() {
		return {
			// 支付宝绑定数据
			mingzi:'',
			idnumber:'',
			imgbefore:'',
			imgreverse:"",
			// usdt绑定字段
			name:"",
			idcast:'',
			frontimg: '',
			reverimg: '',
			tabIndex: 0,
			navList: [
				{
					text: 'USDT',
					state: '0'
				},
				{
					text: '支付宝账号',
					state: '1'
				}
			],
			gg: '',
			zfb:'',
			add: '',
			// style:2,
			step: null, // 后台返回的当前步骤
			cc: 1,
			title: '实名验证',
			icon: '/static/icon/left_whitch.png',
			url_left: '/pages/main/my',
			alipay_params: '',
			style: uni.getStorageSync('type')
		};
	},

	methods: {
		
		// ---------------------------------------------------------------
		// usdt 上传图片
		//正面
		uploadfron() { 
			var self = this;
			uni.chooseImage({
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: service.api.user.updateIdImage, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
						filePath: tempFilePaths[0],
						name: 'file',
						success: (res) => {
							console.log(JSON.parse(res.data), "asfasdf");
							const path = JSON.parse(res.data); //打印出来就知道为什么要用JSON.parse
							self.frontimg = path.data.url;
							console.log(self.frontimg, "asdfasdfa");
							uni.showToast({
								title: path.info,
								icon: 'none'
							});
						}
					});
				}
			});
		},
		//反面
		uploadrever() { 
			var self = this;
			uni.chooseImage({
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: service.api.user.updateIdImage, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
						filePath: tempFilePaths[0],
						name: 'file',
						success: (res) => {
							console.log(JSON.parse(res.data), "asfasdf");
							const path = JSON.parse(res.data); //打印出来就知道为什么要用JSON.parse
							self.reverimg = path.data.url;
							console.log(self.reverimg, "asdfasdfa");
							uni.showToast({
								title: path.info,
								icon: 'none'
							});
						}
					});
				}
			});
		},
		// --------------------------------------------------------------------------------------------
		// 支付宝 上传图片
		//正面
		uploadfrons() { 
			var self = this;
			uni.chooseImage({
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: service.api.user.updateIdImage, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
						filePath: tempFilePaths[0],
						name: 'file',
						success: (res) => {
							console.log(JSON.parse(res.data), "asfasdf");
							const path = JSON.parse(res.data); //打印出来就知道为什么要用JSON.parse
							self.imgbefore = path.data.url;
							console.log(self.imgbefore, "asdfasdfa");
							uni.showToast({
								title: path.info,
								icon: 'none'
							});
						}
					});
				}
			});
		},
		//反面
		uploadrevers() { 
			var self = this;
			uni.chooseImage({
				success: (chooseImageRes) => {
					const tempFilePaths = chooseImageRes.tempFilePaths;
					uni.uploadFile({
						url: service.api.user.updateIdImage, //仅为示例，非真实的接口地址
						header: {
							Authorization: 'Bearer ' + uni.getStorageSync('token')
						},
						filePath: tempFilePaths[0],
						name: 'file',
						success: (res) => {
							console.log(JSON.parse(res.data), "asfasdf");
							const path = JSON.parse(res.data); //打印出来就知道为什么要用JSON.parse
							self.imgreverse = path.data.url;
							console.log(self.imgreverse, "asdfasdfa");
							uni.showToast({
								title: path.info,
								icon: 'none'
							});
						}
					});
				}
			});
		},
		//swiper 切换
		changeTab(e){
			this.tabClick(e.target.current);
		},
		
		//顶部tab点击
		tabClick(index){
			this.change(index);
		},
		change(index){
			this.type = this.navList[index].type;
			this.tabIndex = index;
		},
		// usdtSubmit
		
		// usdt提交按钮
		obtain() {
			var self = this;
			if (self.name == '') {
				uni.showToast({
					icon: 'none',
					title: '名称不能为空'
				});
				return;
			}
			if (self.idcast == '') {
				uni.showToast({
					icon: 'none',
					title: '证件号码不能为空'
				});
				return;
			}
			if (self.frontimg == '') {
				uni.showToast({
					icon: 'none',
					title: '请上传证件正面照片'
				});
				return;
			}
			if (self.reverimg == '') {
				uni.showToast({
					icon: 'none',
					title: '请上传证件反面照片'
				});
				return;
			}
			if (self.add == '') {
				uni.showToast({
					icon: 'none',
					title: '钱包地址不能为空'
				});
				return;
			}	
			uni.request({
				url: service.api.realName.usdtSubmit,
				method: 'POST',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				data: {
					realname:self.name,
					idnum:self.idcast,
					idnum_img:self.frontimg,
					idnum_img_back:self.reverimg,
					usdt: self.add,
					type:1
				},
				success(res) {
					console.log(res.data);
					if (res.data.code == 1) {
						setTimeout(function() {
							uni.showModal({
								title: '温馨提示',
								content: res.data.info,
								showCancel: false,
								success: function(res) {
									if (res.confirm) {
										// self.style = 0
										self.cc = 1;
										self.gg = 1;
										// uni.reLaunch({
										// 	url:'/pages/main/my'
										// })
									}
								}
							});
						}, 1000);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				}
			});
		},
		// usdt 删除图片
		close(e, s) {
			console.log(e)
			var self = this;
			service.auth(self, service.api.user.deleteImg, "POST", {
				path: e
			}, function(_self, res) {
				if (s === 1) {
					_self.frontimg = '';
				} else if (s === 2) {
					_self.reverimg = '';
				}
				console.log(res);
				uni.showToast({
					title: res.info,
					icon: 'none'
				})
			});
		},
		// 支付宝提交按钮
		zfbsbumit() {
			var self = this;
			if (self.mingzi == '') {
				uni.showToast({
					icon: 'none',
					title: '名称不能为空'
				});
				return;
			}
			if (self.idnumber == '') {
				uni.showToast({
					icon: 'none',
					title: '证件号码不能为空'
				});
				return;
			}
			if (self.imgbefore == '') {
				uni.showToast({
					icon: 'none',
					title: '请上传证件正面照片'
				});
				return;
			}
			if (self.imgreverse == '') {
				uni.showToast({
					icon: 'none',
					title: '请上传证件反面照片'
				});
				return;
			}
			if (self.zfb == '') {
				uni.showToast({
					icon: 'none',
					title: '支付宝账号不能为空'
				});
				return;
			}	
			uni.request({
				url: service.api.realName.usdtSubmit,
				method: 'POST',
				header: {
					Authorization: 'Bearer ' + uni.getStorageSync('token')
				},
				data: {
					realname:self.mingzi,
					idnum:self.idnumber,
					idnum_img:self.imgbefore,
					idnum_img_back:self.imgreverse,
					alipay: self.zfb,
					type:2
				},
				success(res) {
					console.log(res.data);
					if (res.data.code == 1) {
						setTimeout(function() {
							uni.showModal({
								title: '温馨提示',
								content: res.data.info,
								showCancel: false,
								success: function(res) {
									if (res.confirm) {
										// self.style = 0
										self.cc = 1;
										self.gg = 1;
										// uni.reLaunch({
										// 	url:'/pages/main/my'
										// })
									}
								}
							});
						}, 1000);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				}
			});
		},
			// 支付宝 删除图片
				closes(e, s) {
					console.log(e)
					var self = this;
					service.auth(self, service.api.user.deleteImg, "POST", {
						path: e
					}, function(_self, res) {
						if (s === 1) {
							_self.imgbefore = '';
						} else if (s === 2) {
							_self.imgreverse = '';
						}
						console.log(res);
						uni.showToast({
							title: res.info,
							icon: 'none'
						})
					});
				},
		nn() {
			this.cc = 1;
		},
		jump(url, step) {
			if (this.step != step) {
				uni.showToast({
					icon: 'none',
					title: '请完成上一步'
				});
				return;
			}
			uni.navigateTo({
				url: url
			});
		},
		//支付接口
		paymentCost() {

			uni.showToast({
				icon: 'none',
				title: '该功能只能在app内使用'
			});































































		},
		// 实名步骤接口
		status() {
			service.auth(this, service.api.realName.getVerificationStep, 'GET', {}, function(self, res) {
				// console.log(res);
				if (res.code == 1) {
					self.step = res.data.step;
				}
				// console.log(self.step);
			});
			this.$forceUpdate();
		}
	},
	onShow() {
		// 获取实名步骤
		this.step = uni.getStorageSync('step');
		const user = uni.getStorageSync('member');
		this.cc = user.is_idverifly;
		this.status();

		/*----获取实名认证的状态---*/
		uni.request({
			//获取实名认证的状态
			url: this.$service.api.index.check_status,
			method: 'GET',
			header: {
				Authorization: 'Bearer ' + uni.getStorageSync('token')
			},
			success: res => {
				// var res = res.data;
			
					this.gg = res.data.data.status;
			
				
				console.log(this.gg,2222)
			}
		});
		//var res = uni.getStorageSync('realName')
		//this.gg = res
		// console.log(this.cc,this.step)
		// var nums = uni.getStorageSync('type')
		//console.log(res,11111)
	},
	onLoad(e) {
		console.log(e);
		//this.style = e.type
		this.status();

		this.$forceUpdate();
	}
};
